html {
  font-size: $fs-base;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  background: $clr-3;
  text-align: left;
  font-family: $ff-1;
  font-weight: 400;
  line-height: $lh-general;
  color: $clr-1;
  
  &:before { // this covers the fake nav background and box-shadow
    position: relative;
    z-index: 998;
    display: block;
    background: white;
    padding: $nav-link-padding 0 ($nav-link-padding + 0.17);
    line-height: $lh-general;
    font-size: $fs-18;
    content: "\00A0";
  } 

  &.article:before,
  &.services:before,
  &.consulting:before,
  &.training:before,
  &.guide:before,
  &.landing:before {padding: $nav-link-padding 0;} // extra 0.17 padding not required on these pages

  &:after { // this is the fake nav background
    position: fixed;
    left: 0; top: 0; right: 0;
    z-index: 997;
    background: white;
    padding: $nav-link-padding 0;
    line-height: $lh-general;
    font-size: $fs-18;
    content: "\00A0";
    box-shadow: 0 1px 2px rgba(black,0.2);
  } 
}

main.content {background: white;}

// hiding

.hide-screen {
  @include hide-screen();

  &.focusable:active,
  &.focusable:focus {
    position: static;
    overflow: visible;
    clip: auto;
    margin: 0;
    width: auto;
    height: auto;
  }
}

.hide-mobile {
  @include bp-s-less {@include hide-screen();}
}

.hide {display: none!important;}

// interactive element focus

a,
button,
input,
select,
textarea {
  &:focus {outline: 1px dotted;}
}

// elements

embed,
iframe,
object {max-width: 100%;}

sup {
  font-size: 0.75rem;
  vertical-align: super;
}

code {
  font-family: monospace;
  line-height: 1.0;
}

figcaption {font-size: $fs-16;}
strong,b {font-weight: 700;}

dl {
  dt {
    margin-top: 0.75rem;

    &:first-child {margin-top: 0;}
  }
}

q {font-style: italic;}

// anchors

a {
  color: $clr-b1;
  text-decoration: underline;

  @include bp-m {
    &:hover,
    &:focus {text-decoration: none;}
  }
}

// buttons

.button.button { 
  display: inline-block;
  border-radius: 0.25rem;
  border: 0;
  padding: 0.5em 1.25em;
  // background-image: linear-gradient(to bottom, #999 0%, #555 100%);
  // background-blend-mode: overlay;
  background-color: $clr-b1;
  text-align: center;
  font-family: $ff-1;
  font-weight: 400;
  line-height: inherit;
  color: white;
  cursor: pointer;
  text-decoration: none;
  text-shadow: none;
  box-shadow: 0 1px 2px rgba(black,0.4), inset 0 0 0 rgba(black,0.05), inset 0 1px 1px rgba(white,0.6);
  outline: none;
  user-select: none;
  -webkit-appearance: none;
  transition: box-shadow 0.1s, background-color 0.1s;

  &[class*="icon-"] {
    &:before {
      margin-right: 0.4rem;
      font-size: 0.8em;
    }
  }

  span {
    display: inline-block;
    vertical-align: baseline;
  }
  
  &.small,
  main div.col-4 form & {font-size: $fs-16;}

  &.big {font-size: $fs-30;}

  &.secondary {
    background: rgba(black,0.2);
    box-shadow: inset 0 0 0 2px white, 0 1px 2px rgba(black,0.4);

    &.alt {
      background: rgba(white,0.5);
      color: $clr-b1;
      box-shadow: inset 0 0 0 3px $clr-b1, 0 1px 2px rgba(black,0.4);

      @include bp-m {
        &:hover,
        &:focus {
          background: rgba(white,0.75);
          box-shadow: inset 0 0 0 3px $clr-b1, 0 0 0 5px rgba(black,0.1);
        }
      }
    }

    @include bp-m {
      &:hover,
      &:focus {
        background: rgba(white,0.05);
        box-shadow: inset 0 0 0 2px white, 0 0 0 5px rgba(black,0.1);
      }

      &:active {
        background: white!important;
        color: $clr-1!important;
        box-shadow: inset 0 1px 2px rgba(black,0.75), 0 0 0 5px rgba(white,0.1)!important;
      }
    }
  }

  @include bp-m {
    &:hover,
    &:focus {box-shadow: 0 0 0 5px rgba(black,0.15), inset 0 -4rem 0 rgba(black,0.05);}

    &:active {
      box-shadow: 0 0 0 5px rgba(white,0.15), inset 0 4rem 0 rgba(black,0.1), inset 0 1px 2px rgba(black,0.75);
      transition: box-shadow 0.25s;
    }
  }

  &.disabled {
    background: rgba(black,0.1);
    color: rgba(black,0.5);
    box-shadow: inset 0 1px 2px rgba(black,0.75), 0 0 0 5px rgba(white,0.1);
    opacity: 0.5;
  }
}

// alignment

p, pre, blockquote, ul, ol, dl, figure, table {margin-bottom: $vmargin;}

// headings

h1,h2,h3,h4,h5,h6 {
  margin-bottom: $vmargin;
  font-weight: 300;
  font-size: $fs-20;
  line-height: $lh-headings;
}

h1,.h1 {
  font-size: $fs-35;
  
  @include bp-s {
    font-size: $fs-40;
    span {display: block;}
  }

  @include bp-m {font-size: $fs-55;}
  @include bp-l {font-size: $fs-70;}
}

.h1-2 {
  @extend .h1;
  @include bp-l {font-size: $fs-55;}
}

h2,.h2 {
  font-weight: 300;
  font-size: $fs-25;
  @include bp-s {font-size: $fs-30;}
  @include bp-m {font-size: $fs-35;}
}

.h2-3 {
  @extend .h2;
  @include bp-l {font-size: $fs-45;}
}

h3,.h3 {
  font-weight: 400;
  font-size: $fs-22;
  @include bp-m {font-size: $fs-27;}

  div.team & {@extend .h2;}
}

h4,.h4 {
  font-weight: 500;
  @include bp-m {font-size: $fs-25;}
}

h5,.h5 {
  font-weight: 500;
  @include bp-m {font-size: $fs-22;}
}

h6,.h6 {font-weight: 700;}

// helpers

.break {display: block;}
.spacer {margin-top: ($vmargin * 2);}
.no-margin {margin: 0;}
.no-wrap {white-space: nowrap;}
.text-wrap {overflow-wrap: break-word;}
.align-center {text-align: center;}
.align-right {text-align: right;}
.small-text {font-size: $fs-16;}
.new-window {cursor: alias;}

.aesthetic {
  background-image: url(../../assets/images/content/other/polygon-mob.jpg);
  @include bp-m {background-image: url(../../assets/images/content/other/polygon.jpg);}
}

.lead {
  font-weight: 300;
  @include bp-s {font-size: $fs-22;}
  @include bp-m {font-size: $fs-25;}
  @include bp-l {
    div.page-intro &,
    div.landing-intro & {font-size: $fs-30;}
  }
}

.target:target:before {
  display: block;
  margin-top: -4rem;
  height: 4rem;
  content: "";
  visibility: hidden;
}

p.statement {
  margin: 1rem 0 0;
  font-size: $fs-16;
}

span.area-code {font-size: 0.75em;}

figure {

  &.headline {
    position: relative;
    overflow: hidden;
    margin-bottom: 0;
    background: $clr-4;

    @include bp-m {max-height: 350px;}

    &:before { // stops page jump
      position: relative;
      z-index: 1;
      display: block;
      padding-top: 56.2%;
      background: $clr-4;
      content: "";

      @include bp-m {padding-top: 28.1%;}
    }

    img {
      position: absolute;
      top: 0; left: 0;
      z-index: 2;
    }
  }

  &.expanded {
    margin-left: -$gutter;
    margin-right: -$gutter;

    @include bp-m {
      overflow: hidden;
      float: right;
      margin: 0 0 $vmargin $spacing;
      border-radius: $border-radius;
      max-width: 60%;
    }
  }

  img {
    display: block;
    width: 100%;
  }
}

p.navigation-jump {
  text-align: center;
  @include bp-m {@include hide-screen();}
}

span.brand-colour {
  display: inline-block;
  padding: 0 0.25rem;
  background: $clr-b1;
  color: white;
}
