// spaced list

.spaced {
  li {margin: 0.5rem 0;}
}

// disc list

.disc {
  margin-left: 1.25rem;
  list-style: disc;

  ul {
    margin-left: 1.25rem;
    list-style: circle;
  }
}

// decimal list

.decimal {
  margin-left: 1.25rem;
  list-style: decimal;
}

// lower alpha list

.lower-alpha li {
  margin-left: 1.25rem;
  list-style: lower-alpha;

  ol {margin-bottom: 0;}
}

// defintion list

dt {
  @extend .h4;
  font-weight: 500;
}

// checked list

ul.checked {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-check;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;

      body.consulting & {color: $clr-b2;}
      body.training & {color: $clr-b3;}
    }
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// buttons

ul.buttons {
  li {
    display: inline-block;
    margin-bottom: 0.5rem;
    margin-right: 0.2rem;
  }
}

// hierarchy links

ol.hierarchy {
  margin-bottom: $vmargin/2;
  font-size: $fs-16;

  li {
    display: inline-block;

    &:before {
      @extend .icon-extension, .icon-chevron-right;
      margin-right: 0.25rem;
      font-size: $fs-10;
      vertical-align: middle;
    }
  
    a, span {
      text-decoration: none;
      vertical-align: middle;
    }
    
    a {
      @include bp-m {
        &:hover,
        &:focus {text-decoration: underline;}
      }
    }

    &:first-child:before {content: none;}
  }
}

// brand assets

ul.brand-assets {
  border-top: 1px solid $clr-3;

  li {
    border-bottom: 1px solid $clr-3;
    padding: 0.4rem 0;

    a {
      text-decoration: none;
    }
  }
}

// chevroned list

ul.chevroned {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-chevron-right;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;

      body.consulting & {color: $clr-b2;}
      body.training & {color: $clr-b3;}
    }
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// document list

ul.documents {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-file-text;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;
    }
  }
}

// features

ul.features {
  margin-bottom: 0;
  border-radius: $border-radius;
  padding: ($spacing*1.5);
  background: $clr-4;

  li {
    position: relative;
    margin-top: 1rem;
    padding-left: 2.5rem;

    &:first-child {margin-top: 0;}

    &:before {
      position: absolute;
      left: 0; top: 0;
      font-size: $fs-27;
      color: $clr-b1;
    }

    h3 {
      @extend .h5;
      margin-bottom: 0.5rem;
    }

    p {
      margin-bottom: 0;
      font-size: $fs-18;
    }
  }
}

// numbered discs

ol.numbered-discs {
  counter-reset: numbered-discs;

  li {
    position: relative;
    margin-bottom: 0.5rem;
    padding-left: 2rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      margin-right: 1rem;
      border-radius: 50%;
      padding: 0;
      width: 1.3rem;
      height: 1.3rem;
      line-height: 1.4rem;
      background: $clr-2;
      text-align: center;
      font-weight: 700;
      font-size: $fs-16;
      color: white;
      counter-increment: numbered-discs;
      content: counter(numbered-discs);

      body.consulting & {background: $clr-b2;}
      body.training & {background: $clr-b3;}
    }
  }
}

// numbered blocks

ol.numbered-blocks {
  counter-reset: numbered-blocks;

  li {
    position: relative;
    margin-bottom: 4px;
    padding: $spacing;
    padding-left: $spacing*2;
    background: $clr-4;

    &:first-child {border-radius: $border-radius $border-radius 0 0;}
    &:last-child {border-radius: 0 0 $border-radius $border-radius;}

    &:before {
      position: absolute;
      right: 100%; top: 50%;
      margin: -1.25rem -1.25rem 0 0;
      border-radius: 50%;
      border: 4px solid white;
      padding: 0;
      width: 2.5rem;
      height: 2.5rem;
      line-height: 2.2rem;
      background: $clr-2;
      text-align: center;
      font-weight: 700;
      font-size: $fs-20;
      color: white;
      counter-increment: numbered-blocks;
      content: counter(numbered-blocks);
      box-sizing: border-box;

      body.consulting & {background: $clr-b2;}
      body.training & {background: $clr-b3;}
    }

    > :last-child {margin-bottom: 0;}
  }
}

// contact details

ul.contact {
  li {
    position: relative;
    padding-left: 1.5rem;

    &:before {
      position: absolute;
      top: 0.1rem; left: 0;
    }

    &.email {
      overflow-wrap: break-word;
      word-wrap: break-word;

      span {
        footer & {@include hide-screen();}
      }
    }

    &.address {
      div.box &,
      div.personalised-support &,
      footer & {@include hide-screen();}
      span {display: block;}
    }

    a {
      text-decoration: none;
      
      main & {
        @include bp-m {
          &:hover,
          &:focus {
            text-decoration: underline;
          }
        }
      }
    }

    span.icon-chevron-right:before {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0.5rem;
      font-size: $fs-10;
    }
  }
}

// site map

ul.site-map {
  overflow: hidden;
  border-radius: $border-radius;

  > li {
    a {
      display: block;
      border-bottom: 1px solid white;
      padding: 0.5rem 1rem;
      background: $clr-4;
      text-decoration: none;
      color: $clr-1;

      @include bp-m {
        &:hover,
        &:focus {
          background: $clr-3;
          color: $clr-b1;
        }
      }
    }

    ul {
      margin: 0;
      font-size: $fs-18;
      
      li a {
        padding-left: 2rem;
        &:before {content: "\021B3\000A0";}
      }

      ul li a {padding-left: 4rem;}
    }

    &:last-child a {border-bottom: 0;}
  }
}

// faqs 

ol.faqs {
  counter-reset: faq-item;

  li {
    margin: 0 0 4px;
    counter-increment: faq-item;

    &:first-of-type h3 label  {border-radius: $border-radius $border-radius 0 0;}
    &:last-of-type h3 label,
    &:last-of-type div.answer  {border-radius: 0 0 $border-radius $border-radius;}
    &:last-of-type input:checked + h3 label {border-radius: 0;}

    input:not(#iefix) {
      @include hide-screen();

      + h3 {
        @extend .h4;
        margin: 0;
        color: $clr-b1; // important for when in a dark background strip
          
        label {
          position: relative;
          display: block;
          margin: 0;
          padding: $spacing;
          padding-right: 3rem;
          background: rgba($clr-4,0.7);
          color: $clr-1;
          cursor: pointer;

          &:before {
            @extend .icon-extension,.icon-chevron-down;
            position: absolute;
            top: 50%; right: 0;
            z-index: 1;
            margin: -0.25rem 1rem 0 0;
            font-size: 0.7em;
            color: $clr-b1;
          }

          @include bp-m {
            &:hover,
            &:focus {
              background: $clr-3;
              color: $clr-b1;
            }
          }

          span:before {
            content: counter(faq-item) ". ";
          }
        }

        + div.answer {
          position: absolute;
          left: -1000em;
          padding: $spacing;
          background: $clr-4;
          color: $clr-1;
          -webkit-text-size-adjust: 100%; // forcing iOS to stop increasing text size

          :last-child {margin-bottom: 0;}
        }
      }

      &:checked {
        + h3 {

          label {
            background: $clr-2;
            color: white;

            &:before {
              @extend .icon-times;
              color: white;
            }
          }

          + div.answer {position: static;}
        }
      }
    }
  }
}

// direct funnels

ul.direct-funnels {
  position: relative;
  text-align: center;

  li.col.col {
    flex: 1 1 auto;

    a {
      @extend .h2;
      display: block;
      padding: ($spacing*1.5) $spacing;
      background: rgba($clr-1,0.25);
      color: white;
      text-decoration: none;
      cursor: pointer;
      transition: background-color 0.2s;

      @include bp-m {
        display: flex;
        flex: 1; 
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: ($spacing*2.5) $spacing;
        min-height: 10rem;

        &:hover,
        &:focus {
          background: rgba($clr-1,0.35);
          text-decoration: underline;
        }
      }

      span {
        display: block;
        margin-top: -1rem;

        span {
          display: inline-block;
          margin-bottom: 0.5rem;
          border-radius: 2rem;
          padding: 0.25rem 1rem;
          background: rgba(black,0.2);
          font-size: $fs-16;
          font-weight: 500;
          color: white;
        }
      }
    }
  }
}

// description funnels

ul.description-funnels {
  position: relative;
  text-align: center;

  li.col.col {
    flex: 1 1 auto;

    a {
      display: block;
      padding: $spacing*1.5;
      background: $clr-4;
      color: $clr-1;
      text-decoration: none;
      cursor: pointer;
      transition: background-color 0.2s;

      @include bp-m {
        display: flex;
        flex-direction: column;
        flex: 1;

        &:hover h2,
        &:focus h2 {text-decoration: underline;}
      }

      h2 {
        margin-bottom: $vmargin/2;
        color: $clr-b1;

        span {
          display: block;
          margin-top: -1rem;

          span {
            display: inline-block;
            margin-bottom: 0.5rem;
            border-radius: 2rem;
            padding: 0.25rem 1rem;
            background: rgba(white,1);
            font-size: $fs-16;
            font-weight: 500;
            color: $clr-2;
          }
        }
      }

      @include bp-m {
        &:hover,
        &:focus {background: $clr-3;}
      }

      p {
        margin: 0 auto;
        max-width: 650px;
      }
    } 
  }
}

// team

ul.team {
  position: relative;
  text-align: center;

  > li {
    position: relative;
    @include bp-m {position: static;}

    div.content {
      display: flex;
      flex: 1;

      h4 {
        @extend .h2;
        position: relative;
        display: flex;
        flex: 1;
        margin: 0;

        label {
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          flex: 1;
          padding: ($spacing*1.5) $spacing;
          background: $clr-4;
          cursor: pointer;
          transition: background-color 0.2s;

          @include bp-m {
            padding: ($spacing*2.25) $spacing;

            &:hover,
            &:focus {
              background: $clr-3;

              span.name {text-decoration: underline;}
            }
          }

          @include bp-l {
            padding: ($spacing*1.75) $spacing;
          }

          span.image {
            display: block;
            position: relative;
            overflow: hidden;
            margin: 0 auto $vmargin;
            border-radius: 50%;
            width: 200px;

            img {
              position: absolute;
              top: 0; left: 0;
              border-radius: 50%;
              width: 100%;
            }

            &:before {
              display: block;
              padding-top: 100%;
              content: "";
            }
          }

          span {
            &.name {color: $clr-b1;}

            &.title {
              display: block;
              margin-top: 0.25rem;
              font-weight: 400;
              font-size: $fs-18;
            }
          }
        }

        a.linkedin.linkedin {
          display: none;
          margin-top: 0.75rem;
          padding-left: 0.6rem;
          padding-right: 0.6rem;

          @include bp-m {
            position: absolute;
            top: 0; left: 50%;
            margin: 75px 0 0 125px;
          }

          &:before {
            margin: 0;
            font-size: $fs-20;
            vertical-align: baseline;
            color: white;
          }

          span {@include hide-screen();}
        }
      }

      div.text {
        position: absolute;
        left: -1000em;
        align-items: flex-start;
        text-align: left;
        color: white;

        a.button span {@include hide-screen();}

        ul.checked,
        ul.chevroned {
          margin-bottom: 0;

          li {
            margin-bottom: 0;
            &:before {color: $clr-b1;}

            a {
              color: white;
              text-decoration: none;

              @include bp-m {
                &:hover,
                &:focus {text-decoration: underline;}
              } 
            }
          }
        }

        > div:last-child p {margin-bottom: 0;}
      }
    }

    input:not(#iefix) {
      @include hide-screen();

      &:checked + div.content {
        display: block; // reset flex
        padding: ($spacing*1.5) $spacing;
        background: $clr-2;

        @include bp-m {
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          z-index: 1; // required
          padding: $spacing*2;
        }

        &:after {
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          z-index: 1;
          @extend .aesthetic;
          background-repeat: no-repeat;
          background-position: 50%;
          background-size: cover;
          opacity: 0.1;
          content: "";
        }

        h4,
        div.text {
          position: relative;
          z-index: 2;
          left: auto;
          > :last-child {margin-bottom: 0;}
        }

        h4 {
          display: block; // reset flex
          margin-bottom: ($vmargin/2);
          color: white;

          @include bp-m {margin-bottom: $vmargin;}

          label {
            display: block;
            padding: 0;
            min-height: 0;
            background: none;
            cursor: default;
            transition: none;

            @include bp-m {
              display: inline-block;

              &:hover,
              &:focus {
                span.name {text-decoration: none;}
              }
            }

            &:before {
              @extend .icon-extension,.icon-times;
              position: absolute;
              right: -0.5rem; top: -1rem;
              border-radius: 50%;
              width: 1.5rem;
              height: 1.5rem;
              line-height: 1.5rem;
              background: white;
              text-align: center;
              font-size: $fs-16;
              color: $clr-2;
              cursor: pointer; 

              @include bp-m {
                right: -1rem; top: -1rem;
                width: 2rem;
                height: 2rem;
                line-height: 2.05rem;
                font-size: $fs-18;
              }
            }

            &:hover:before {
              background: $clr-b1;
              color: white;
              transition: color 0.2s, background-color 0.2s;
            }

            span.name {color: white;}
          }

          a.linkedin {display: inline-block;}
        }
      }
    }
  }
}

// testimonials

div.testimonials {
  margin: $vmargin 0;
  background: url(../../assets/images/content/other/world-map-mob.png) no-repeat 50%;
  background-size: auto 100%;

  @include bp-m {
    margin: ($vmargin*2) 0;
    background-image: url(../../assets/images/content/other/world-map.png);
  }

  ul {
    margin-bottom: 0;
    text-align: center;

    li {

      input:not(#iefix) {
        position: fixed;
        top: 50%; left: -1000em;
        z-index: -1;

        &:checked {
          + div.content {
            overflow: visible;
            visibility: visible;
            margin-left: 0;
            padding: 1.25rem 0;
            width: 100%;
            max-height: 30rem;
            opacity: 1;
            transition: opacity 0.25s 0.25s, margin-left 0.25s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

            @include bp-m {padding: 3rem 0;}
          }
        }
      }

      div.content {
        display: flex;
        justify-content: center;
        overflow: hidden;
        visibility: hidden;
        margin-left: -20rem;
        padding: 0;
        width: 0;
        max-height: 0;
        opacity: 0;
        transition: opacity 0.25s, margin-left 0.25s, visibility 0s 0.25s, width 0s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

        blockquote {
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-bottom: 0;
          border-left: 5px solid $clr-b1;
          border-radius: $border-radius;
          padding: $spacing ($spacing/1.7);
          max-width: 650px;
          min-height: 18rem;
          background: rgba($clr-4,0.5);

          @include bp-s {
            padding: $spacing;
            min-height: 14rem;
          }

          @include bp-m {padding: $spacing ($spacing*1.5);}

          p {
            @extend .h4;

            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          }

          cite {
            font-style: normal;
            @include bp-m-less {font-size: $fs-18;}
            span {display: block;}
          }
        }

        label.button {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          right: 50%; top: 100%;
          z-index: 10;
          margin: -1.25rem -1.25rem 0 0;
          border-radius: 3rem;
          padding: 0!important;
          width: 2.5rem;
          height: 2.5rem;

          @include bp-m {
            right: 0; top: 50%;
            margin: -1.25rem -1.25rem 0 0;
          }

          &:before {
            @extend .icon-extension, .icon-chevron-right;
          }

          span {@include hide-screen();}
        }
      }
    }
  }
}

// downloads

ul.downloads {
  margin-bottom: 0;

  > li {
    margin-top: 4px;
    &:first-child {margin-top: 0;}

    div.content {
      flex: 1;
      display: block;
      padding: $spacing*1.5;
      background: $clr-4;

      h3 {
        margin-bottom: $vmargin/2;

        span {
          display: block;

          b {
            display: inline-block;
            margin-bottom: 0.5rem;
            border-radius: 0.2rem;
            padding: 0.15rem 0.5rem;
            background: white;
            font-weight: 500;
            font-size: $fs-16;
          }
        }
      }

      > :last-child {margin-bottom: 0;}
    }
  }
}

// events

ol.events {
  text-align: left;

  li.col {

    div.content {
      flex: 1;
      display: block;
      padding: $spacing;
      background: $clr-4;

      div.section.style-5 & {background: rgba(white,0.5);}
      
      @include bp-m {padding: $spacing*1.5;}

      > :last-child {margin-bottom: 0;}

      h3 {

        span {
          display: block;

          b {
            display: inline-block;
            margin-bottom: 0.5rem;
            border-radius: 0.2rem;
            padding: 0.15rem 0.5rem;
            background: $clr-b3;
            font-weight: 500;
            font-size: $fs-16;
            color: white;
          }
        }

        a {
          display: inline-block;
          text-decoration: none;

          @include bp-m {
            &:hover,
            &:focus {text-decoration: underline;}
          }
        }
      }

      p.cta {
        a.button span {@include hide-screen();}
      }
    }
  }
}

// events details

ul.events-details {
  overflow: hidden;
  border-radius: $border-radius;
  padding: 1rem;
  background: $clr-4;
  font-size: $fs-18;

  ol.events & {background: white;}

  @include bp-s {padding: 0;}
 
  li {
    @include bp-s {display: flex;}

    b {
      color: $clr-b3;
      font-weight: 500;

      @include bp-s {
        display: inline-block;
        flex: 0 0 auto;
        padding: 0.5rem;
        min-width: 9rem;
        background: $clr-3;
        text-align: right;
      }
    }

    span {

      @include bp-s {
        display: inline-block;
        flex: 1;
        padding: 0.5rem;
        box-shadow: inset 0 -1px rgba(black,0.05);
      }

      small {font-size: $fs-16;}
    }

    &:last-child > * {box-shadow: none;}
  }
}

// articles

ol.articles {

  li.col {

    a {
      display: block;
      background: $clr-4;
      text-decoration: none;
      transition: background-color 0.2s;

      @include bp-m {
        &:hover,
        &:focus {
          background: $clr-3;

          h3 {text-decoration: underline;}
        }
      }

      figure {
        position: relative;
        z-index: 1;
        margin-bottom: 0;

        &:before { // stops page jump on slow loading of images
          position: relative;
          z-index: 1;
          display: block;
          padding-top: 56.3%;
          background: $clr-3;
          content: "";
        }

        img {
          position: absolute;
          top: 0; left: 0;
          z-index: 2;
        }
      }

      div.text {
        padding: $spacing;
        
        h3 {
          @extend .h4;
          margin-bottom: $vmargin/2;
          font-weight: 400;
        }

        p {
          margin-bottom: 0;
          color: $clr-1;

          time {font-size: $fs-14;}
        }
      }
    }
  }
}

// information

ul.information {

   div.content {
    flex: 1;
    background: rgba(white,0.5);

    h3 {
      @extend .h2;
      margin: 0;
      padding: ($spacing*0.75) $spacing;
      background: rgba(white,0.6);
      text-align: center;
    }

    p {
      margin: 0;
      padding: $spacing;
      font-size: $fs-22;

      @include bp-m {padding: $spacing*1.5;}

      em {
        display: block;
        margin-bottom: $vmargin/2;
        font: italic $fs-30 Georgia, Serif;
      }
    }
  }

  &.alt div.content {background: $clr-4;}
}

// guide navigation

body.guide {
  main div.strip,
  footer.footer {
    position: relative;
    z-index: 3;

    &.guide-intro,
    &.guide-content {z-index: 1;}
  }

  nav.guide-contents {z-index: 2;}
}

input.guide-contents-toggle:not(#iefix) {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;

  &:checked {
    + nav.guide-contents {
    
      @include bp-s-less {
        top: 0;
        z-index: 2000;
        padding-bottom: 0;
      }

      label {
        cursor: pointer;

        @include bp-s-less {
          position: absolute;
          left: 0; right: 0; bottom: 1.25rem;
          z-index: 12;
        }

        > span {
          @include bp-s {box-shadow: inset 0 1px 2px rgba(0,0,0,0.75), 0 0 0 5px rgba(white,0.1);}

          &:before {@extend .icon-times;}
        }

        &:after { // allow the rest of the page to click/close the nav
          position: fixed;
          left: 0; right: 0; bottom: 0; top: 0;
          z-index: 10;
          background: rgba(black,0.3);
          content: "";

          @include bp-s-less {content: none;}
        }

        + div.guide-links {
          bottom: 0;
          
          @include bp-s-less {
            top: 0;
            height: auto;
          }

          @include bp-s {
            margin-bottom: 4rem;
            opacity: 1;
            transition: margin-bottom 0.25s, opacity 0.25s;
          }

          > div {
            @include bp-s-less {
              overflow-y: auto;
              position: absolute;
              top: 0; left: 0; bottom: 0; right: 0;
              -webkit-overflow-scrolling: touch;

              > ul {padding-bottom: 4rem;}
            }
          }
        }
      }

      + div.guide-intro p.cta label:before {@extend .icon-times;}
    }
  }
}

nav.guide-contents {
  position: fixed;
  left: 0; right: 0; bottom: 0;
  z-index: 500;
  padding-bottom: $spacing;
  text-align: center;

  label {
    > span {
      position: relative;
      z-index: 11;
      font-size: $fs-16;

      @include bp-s {font-size: $fs-18;}

      @include bp-s-less {
        > span {
          text-transform: capitalize;
          > span {@include hide-screen();}
        }
      }
    }
  }

  div.guide-links {
    position: absolute;
    left: 0; right: 0; bottom: -1000em;
    z-index: 11;
    height: 0;
    display: flex;
    align-items: flex-end;
    justify-content: center;

    @include bp-s {
      margin-bottom: 2rem;
      opacity: 0;
      transition: margin-bottom 0.25s, opacity 0.25s, bottom 0s 0.25s;
    }

    > div {
      flex: 0 1 auto;
      background: $clr-2;
      
      @include bp-s {
        border-radius: 0.5rem;
        box-shadow: 0.25rem 0.25rem 1.5rem rgba(black,0.3);
      }

      > ul {
        display: inline-block;
        margin-bottom: 0;
        padding: 0.75rem 0.5rem;
        font-size: $fs-16;
        text-align: left;

        @include bp-s {white-space: nowrap;}

        ul {
          margin-bottom: 0;

          li {
            margin-left: 1.1rem;
            border-left: 1px solid rgba(white,0.2);
          }
        }

        > li > a { // first link
          border-radius: 0.25rem;
          padding: 0.2rem 0.5rem;
          font-weight: 500;

          &:before {content: none;}
        }
      }

      a {
        display: flex;
        border-radius: 0 0.25rem 0.25rem 0;
        padding: 0.2rem 0.5rem 0.2rem 0;
        text-decoration: none;
        color: white;
        
        @include bp-m {
          &:hover,
          &:focus {background: rgba(white,0.075);}
        }

        &.current {background: rgba(black,0.3);}

        &:before {
          flex: 0 1 auto;
          color: rgba(white,0.2);
          content: "\02014\000A0";
        }

        span {flex: 1 1 auto;}
      }
    }
  }
}

// share options

ul.share-options {
  margin-bottom: 0;

  li {
    display: inline-block;
    margin-right: 0.25rem;
    vertical-align: baseline;

    a {
      display: block;
      border-radius: 0.2rem;
      width: 1.7rem;
      height: 1.7rem;
      line-height: 1.85rem;
      text-decoration: none;
      text-align: center;
      font-size: $fs-14;
      color: white;
      transition: box-shadow 0.1s;

      &.icon-linkedin {background: #0077B5;}
      &.icon-twitter {background: #55ADEE;}
      &.icon-facebook {background: #4267B2;}
      &.icon-envelope {background: #7D7D7D;}

      @include bp-m {
        &:hover,
        &:focus {box-shadow: inset 0 0 0 4rem rgba(black,0.1);}
      }

      span {@include hide-screen();}
    }
  }
}
